.card_login_form{
    width: 70vw;
    margin-top: 150px;
    margin-bottom: 30px;
}
.card_register{
    width: 70vw;
    margin-top: 140px;
    margin-bottom: 30px; 
    height: auto;
}
.card_login_form,.card_register h1{
    text-align: center;
    margin-top: 20px;
}

.login_form_container{
    margin: 20px;
    padding: 20px;
}

.login_form_container .input{
    margin-top: 30px;
    height: auto;
}

.login_form_container .label{
    color: #D32F2F;
    border: 1px solid #FFCDD2;
    border-radius: 8px;
    width: 240px;
    padding: 5px;
    margin-top: 10px;
    display: block;   
}
.login_form_container .btn{
    display: flex;
    justify-content: flex-end;
    margin: 30px 0px;
    margin-bottom: 20px;
}

@media(max-width: 800px){
    .card_register{
        width: 90%;
    }
    .card_login_form{
        width: 90%;
    }
}