.responsive_navbar{
    left: 0;
    position: fixed;
    width: 50%;
    background-color: black;
    height: 100vh;
    z-index: 1000;
    overflow-y: scroll;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin-top: 0px;
    top: 0;
}
.responsive_navbar::-webkit-scrollbar {
    display: none;
  }
  
  .responsive_navbar {
    -ms-overflow-style: none;  
    scrollbar-width: none;  
  }
.responsive_navbar p{
    font-size: 30px;
    margin-bottom: 30px;
    color: white;
}
.responsive_navbar .item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    margin-top: 10px;
    border-bottom: 1px solid lightgray;
    padding: 10px;
    cursor: pointer;

}

@media(max-width: 500px){
    .responsive_navbar{
        width: 65%;
    }
    .responsive_navbar p{
        font-size: 20px;
        margin-bottom: 30px !important;
        
    }
    .responsive_navbar .item{
        margin-bottom: 15px;
        margin-top: 15px;
        padding: 10px;
    }
}

@media(max-width: 400px){
    .responsive_navbar{
        /* margin-top: 2px; */
    }
    .responsive_navbar .item{
        margin-bottom: 20px;
        margin-top: 20px;
        padding: 20px;
    }
}